import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import GoogleButton from 'react-google-button';
import { myFirebase } from '../firebase/firebase';
import styled, { keyframes } from 'styled-components';
import logo from './images/decagon.svg';

import { loginUser, log2, logE } from "../actions/";

class Login extends Component {
    constructor(props) {
        super(props);

        this.state = {
            light: true,
        }
    }

    componentDidMount() {
        const { dispatch } = this.props;

        myFirebase.auth().getRedirectResult().then(function(result) {
            /*
            if(result.credential) {
                var token = result.credential.accessToken;
            }
            */
            var user = result.user;
            if (user) {
                dispatch(log2(result.user));
            }
        }).catch(function(e) {
            dispatch(logE());
            console.log('Error Logging In With Google. tbh, idk how this happened.');
        })
    }

    handleSubmit = () => {
        const { dispatch } = this.props;
        dispatch(loginUser());
    };

    render() {
        const { loginError, isLoggingIn, isAuthenticated } = this.props;
        if (isAuthenticated) {
            return <Redirect to='/' />;
        } else {
            return (
                <MainContainer light={this.state.light}>
                    <Logo src={logo} alt='Logo' light={this.state.light} />
                    <Title light={this.state.light}>
                        oro
                    </Title>
                    <GoogleButton
                        type='light'
                        onClick={this.handleSubmit}
                    />
                    {isLoggingIn && <Note>Redirecting...</Note>}
                    {loginError && <Note>Login Error</Note>}
                </MainContainer>
            )
        }
    }
}

function mapStateToProps(state) {
    return {
        isLoggingIn: state.auth.isLoggingIn,
        loginError: state.auth.loginError,
        isAuthenticated: state.auth.isAuthenticated
    };
}

const Rotate = keyframes`
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
`;

const Logo = styled.img`
    height: 60vh;
    margin-top: -1%;
    margin-bottom: -10%;
    animation: ${Rotate} 60s linear infinite;
    filter: invert(${props => props.light});
`;

const Title = styled.p`
    font-family: 'Spartan';
    color: ${props => (props.light) ? '#424242' : '#d4d4d4'};
    font-size: 140px;
    line-height: 0;
    margin-bottom: 8%;
    font-weight: 100;
`;

const Note = styled.p`
    font-family: 'Spartan';
    color: #424242aa;
    font-size: 14px;
`;

const MainContainer = styled.div`
    background-color: ${props => (props.light) ? '#f5f5f5' : '#242429'};
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
`;

export default connect(mapStateToProps)(Login);