import React, { Component } from "react";
import styled from 'styled-components';
import '../nonStyledCSS/buttonSpan.css';

class SettingsModal extends Component {
    constructor(props) {
        super(props)
        this.state = {
            delAcc: false,
        }

        this.inputRef = React.createRef();

        this.palette = [
            // Primary, Secondary, Border, Accent, Accent Hover, Font, Highlight, 
            // 0,       1,         2,      3,      4,            5,    6          

            ['#f5f5f5', '#f0f0f0', '#b9b9b9', '#5672e8', '#4a86ff', '#242429', '#696969aa',
             '#d44f35', '#f5230c',],
            ['#2c2d33', '#242429', '#7d7d85', '#1be3f2', '#00ffff', '#f0f0f0', '#ffffff1a',
             '#cc3318', '#f54114',],
        ];
    }

    promptConfirmation = () => {
        this.setState({ delAcc: true });
    }

    render() {
        var pal = this.palette[this.props.t];

        // ul, li, a, {svg, span text}
        return (
            <SettingBar
                secondary={pal[1]}
                border={pal[2]}
                font={pal[5]}
            >
                <T><u>Options</u></T>
                <Option>
                    <StyledLabel>
                        Dark Theme: 
                    </StyledLabel>
                    <Toggler>
                        <CheckInp
                            type="checkbox"
                            defaultChecked={!!this.props.t}
                            highlight={pal[3]}
                            onClick={()=>{this.props.themeHandler()}}
                        />
                        <span className='slider' />
                    </Toggler>
                </Option>
                <Option>
                    <StyledLabel>
                        Alphabetical: 
                    </StyledLabel>
                    <Toggler>
                        <CheckInp
                            type="checkbox"
                            defaultChecked={!!this.props.a}
                            highlight={pal[3]}
                            onClick={()=>{this.props.sortHandler()}}
                        />
                        <span className='slider' />
                    </Toggler>
                </Option>
                <Breaker border={pal[2]} />
                <Option>
                    <StyledLabel>
                        Report Bug:
                    </StyledLabel>
                    <span className="material-icons" id="ico-button"
                        onClick={() => this.reportBug()}
                    >
                        report_problem
                    </span>
                </Option>
                <Breaker border={pal[2]} />
                <Option>
                    <StyledLabel>
                        Delete Account:
                    </StyledLabel>
                    <span className="material-icons" id="ico-button"
                        onClick={() => this.promptConfirmation()}
                    >
                        remove_circle_outline
                    </span>
                </Option>
                <Breaker border={pal[2]} />
                <Option>
                    <StyledLabel>
                        Log Out:
                    </StyledLabel>
                    <span className="material-icons" id="ico-button"
                        onClick={() => {this.props.logOff()}}
                    >
                        exit_to_app
                    </span>
                </Option>
                <Breaker border={pal[2]} />
                <StyledButton
                    onClick={() => {this.props.returner()}}
                    color={pal[5]}
                    accent={pal[3]}
                >
                    Return
                </StyledButton>
            </SettingBar>
        );
    }
}

export default SettingsModal;

const SettingBar = styled.div`
    background-color: ${props => props.secondary};
    position: absolute;
    top: 0;
    left: 0;
    width: 200px;
    height: calc(100vh - 24px);
    background-color: ${props => props.secondary};
    border-right: 2px solid ${props => props.border};
    color: ${props => props.font};
    font-family: Spartan;
    font-weight: 500;
    font-size: 20px;
    padding-top: 24px;
    padding-left: 12px;
    padding-right: 12px;
    z-index: 1;
`;

const T = styled.div`
    margin-bottom: 20px;
`;

const Option = styled.div`
    display: flex;
    align-items: center;
    font-size: 14px;
    margin-top: 14px;
    margin-bottom: 14px;
    margin-left: 16px
`;

const StyledLabel = styled.div`
    position: relative;
    left: 0;
    font-size: 14px;
`;

const StyledButton = styled.button`
    text-align: center;
    color: ${props => props.color};
    border-radius: 5px;
    border: 1.3px solid ${props => props.color};
    width: 165px;
    background: transparent;
    margin-left: 16px;
    margin-top: 16px;
    padding-top: 8px;
    padding-bottom: 7px;
    font-weight: 600;
    font-family: Spartan;
    font-size: 14px;
    cursor: pointer;
    transition: color 0.2s, border 0.2s;
    :hover {
        border: 1.3px solid ${props => props.accent};
        color: ${props => props.accent};
    }
`;

const Breaker = styled.hr`
    border: none;
    border-top: 1px dotted ${props => props.border};
`;

const Toggler = styled.label`
    position: absolute;
    display: inline-block;
    width: 32px;
    height: 18px;
    right: 32px;
`;

const CheckInp = styled.input`
    opacity: 0;
    width: 0;
    height: 0;
    &:checked + span {
        background-color: ${props => props.highlight};
    }
    &:focus + span {
        background-color: ${props => props.highlight};
    }
    :checked + span:before {
        -webkit-transform: translateX(14px);
        -ms-transform: translateX(14px);
        transform: translateX(14px);
      }
`;