import React, { Component } from "react";
import styled from 'styled-components';
import $ from 'jquery';
import '../nonStyledCSS/textArea.css';

class TodoModal extends Component {
    constructor(props) {
        super(props)
        this.state = {
            newTodo: true,
            title: 'New Todo',
            dueDate: false,
            description: '',
            newComment: '',
            parent: this.props.lists[0],
        }

        this.currentRow = 1;

        this.titleRef = React.createRef(); //title
        this.dateRef = React.createRef();  //due date
        this.descRef = React.createRef();  //description
        this.parentRef = React.createRef(); //parent list

        this.palette = [
            // Primary, Secondary, Border, Accent, Accent Hover, Font, Highlight, 
            // 0,       1,         2,      3,      4,            5,    6          

            ['#f5f5f5', '#f0f0f0', '#b9b9b9', '#5672e8', '#4a86ff', '#242429', '#6969691a',
             '#d44f35', '#f5230c',],
            ['#2c2d33', '#242429', '#7d7d85', '#1be3f2', '#00ffff', '#f0f0f0', '#ffffff1a',
             '#cc3318', '#f54114',],
        ];
    }

    componentDidMount() {
        $(document)
            .one('focus.autoExpand', 'textarea.autoExpand', function(){
                var savedValue = this.value;
                this.value = '';
                this.baseScrollHeight = this.scrollHeight;
                this.value = savedValue;
            })
            .on('input.autoExpand', 'textarea.autoExpand', function(){
                var rows;
                this.rows = 1;
                rows = Math.floor((this.scrollHeight - this.baseScrollHeight) / 18);
                this.rows = rows + 1;
                this.rows -= Math.floor((this.rows - 1) / 6);
            });
        /*
        var t = this.titleRef.current.value;
        var date = this.dateRef.current.value;
        var desc = this.descRef.current.value;
        */
    }

    exitModal = () => {
        this.props.close();
        if (this.titleRef.current) {
            this.titleRef.current.value = '';
            this.dateRef.current.value = '';
            this.descRef.current.value = '';
        }
        if (this.parentRef.current) {
            this.parentRef.current.value = '';
        }
    }

    titleChange = (e) => {
        this.setState({ title: e.target.value });
    }

    dateChange = (e) => {
        this.setState({ dueDate: e.target.value });
    }

    submit = () => {
        if (!this.props.unspecified) {
            this.props.subHandler(
                this.state.title,
                this.state.dueDate,
                this.descRef.current.value,
                this.props.k[this.parentRef.current.value],
            );
        } else {
            this.props.subHandler(
                this.state.title,
                this.state.dueDate,
                this.descRef.current.value
            );
        }
        this.exitModal();
    }

    render() {
        var pal = this.palette[this.props.t];

        let opts = [];
        for (let i = 0; i < this.props.lists.length; i++) {
            let val = this.props.lists[i];
            opts.push(<StyledOption key={i} value={i}>{val}</StyledOption>);
        }

        // ul, li, a, {svg, span text}
        return (
            <NewListModal vis={this.props.v}>
                <Frame
                    border={pal[2]}
                    back={pal[0]}
                >
                    <Holder>
                        <LeftHolder>
                            <Prompt size={32} color={pal[5]}>
                                Title:
                            </Prompt>
                            <TitleInput
                                ref={this.titleRef}
                                border={pal[2]}
                                color={pal[5]}
                                size={32}
                                placeholder={"New Todo Title"}
                                onChange={e => this.titleChange(e)}
                            >
                            </TitleInput>
                        </LeftHolder>
                        <Filler />
                        <RightHolder title='(optional)' >
                            <Prompt size={32} color={pal[5]}>
                                Due Date:
                            </Prompt>
                            <DateSelector
                                ref={this.dateRef}
                                type='date'
                                min={new Date().toISOString().split('T')[0]}
                                border={pal[2]}
                                color={pal[5]}
                                onChange={e => this.dateChange(e)}
                            />
                        </RightHolder>
                    </Holder>
                    <Breaker />
                    {(this.props.unspecified === 0) &&
                        <div>
                            <ListSelector>
                                <Prompt color={pal[5]} size={24}>
                                    Parent list:
                                </Prompt>
                                <Dropdown
                                    ref={this.parentRef}
                                    color={pal[5]}
                                    border={pal[2]}
                                >
                                    {opts}
                                </Dropdown>
                            </ListSelector>
                            <Breaker />
                        </div>
                    }
                    <DescHolder>
                        <Prompt color={pal[5]} size={24}>
                            New Todo Description:
                        </Prompt>
                        <textarea
                            style={{
                                color: pal[5],
                            }}
                            maxLength='400'
                            title='Max Length: 400 characters.'
                            rows='1'
                            className='autoExpand'
                            ref={this.descRef}
                            border={pal[2]}
                            color={pal[5]}
                            size={28}
                            placeholder={"(Optional Description)"}
                        >
                        </textarea>
                    </DescHolder>
                    <Breaker />
                    <StyledButton
                        color={pal[7]}
                        accent={pal[8]}
                        onClick={this.exitModal}
                    >
                        Cancel
                    </StyledButton>
                    <StyledButton
                        color={pal[3]}
                        accent={pal[4]}
                        onClick={this.submit}
                    >
                        Create
                    </StyledButton>
                </Frame>
            </NewListModal>
        );
    }
}

export default TodoModal;

