import React, { Component } from "react";
import styled from 'styled-components';

class DelListModal extends Component {
    constructor(props) {
        super(props)
        this.state = {
            newList: true,
            newListName: '',
        }

        this.inputRef = React.createRef();

        this.palette = [
            // Primary, Secondary, Border, Accent, Accent Hover, Font, Highlight, 
            // 0,       1,         2,      3,      4,            5,    6          

            ['#f5f5f5', '#f0f0f0', '#b9b9b9', '#5672e8', '#4a86ff', '#f0f0f0', '#6969691a',
             '#d44f35', '#f5230c',],
            ['#2c2d33', '#242429', '#7d7d85', '#1be3f2', '#00ffff', '#f0f0f0', '#ffffff1a',
             '#cc3318', '#f54114',],
        ];
    }

    handleNewList = () => {
        this.props.listHandler();
    }

    handleSub = () => {
        this.props.submit();
        this.props.listHandler();
    }

    render() {
        var pal = this.palette[this.props.t];

        // ul, li, a, {svg, span text}
        return (
            <NewListModal>
                <ListPrompt color={pal[5]}>
                    Are You Sure You Want To Delete This List?
                </ListPrompt>
                <Options>
                    <StyledButton
                        onClick={() => {this.handleNewList()}}
                        color={pal[3]}
                        accent={pal[4]}
                    >
                        Cancel
                    </StyledButton>
                    <StyledButton
                        onClick={() => {this.handleSub()}}
                        color={pal[7]}
                        accent={pal[8]}
                    >
                        Delete
                    </StyledButton>
                </Options>
            </NewListModal>
        );
    }
}

export default DelListModal;

const NewListModal = styled.div`
    display: flex;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background: #00000042;
    background: radial-gradient(circle, #00000042 0%, #00000069 85%);;
    align-items: center;
    justify-content: center;
    flex-direction: column;
`;

const ListPrompt = styled.p`
    font-family: Spartan;
    font-size: 38px;
    font-weight: 400;
    color: ${props => props.color};
`;

const StyledButton = styled.button`
    text-align: center;
    color: ${props => props.color};
    border-radius: 5px;
    border: 1.3px solid ${props => props.color};
    width: 111px;
    background: transparent;
    margin-left: 16px;
    margin-right: 16px;
    padding-top: 10px;
    padding-bottom: 9px;
    font-weight: 600;
    font-family: Spartan;
    font-size: 14px;
    cursor: pointer;
    transition: color 0.2s, border 0.2s;
    :hover {
        border: 1.3px solid ${props => props.accent};
        color: ${props => props.accent};
    }
`;

const Options = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`;



