import React, { Component } from "react";
import styled from 'styled-components';

class ListModal extends Component {
    constructor(props) {
        super(props)
        this.state = {
            newList: true,
            newListName: '',
        }

        this.inputRef = React.createRef();

        this.palette = [
            // Primary, Secondary, Border, Accent, Accent Hover, Font, Highlight, 
            // 0,       1,         2,      3,      4,            5,    6          

            ['#f5f5f5', '#f0f0f0', '#696969', '#5672e8', '#4a86ff', '#f0f0f0', '#f0f0f0',
             '',],
            ['#2c2d33', '#7d7d85', '#7d7d85', '#24c97c', '#00ffff', '#f0f0f0', '#f0f0f0',
             '',],
        ];
    }

    componentDidMount() {
        var inp = this.inputRef.current;
        inp.addEventListener('keyup', (e) => {
            if (e.keyCode === 13) {
                e.preventDefault();
                this.props.submit(inp.value);
                this.handleNewList();
            }
        })
    }

    handleNewList = () => {
        this.props.listHandler();
        var inp = this.inputRef.current;
        if (inp) {
            inp.value = '';
        }
    }

    inputChange = (e) => {
        this.setState({ newListName: e.target.value });
    }

    render() {
        var pal = this.palette[this.props.t];

        // ul, li, a, {svg, span text}
        return (
            <NewListModal>
                <Exit onClick={() => this.handleNewList()}>
                    <span className="material-icons" style={{
                        color: '#eb3b28',
                        fontSize: '36px',
                        marginLeft: '4px',
                        marginTop: '4px',
                        cursor: 'pointer',
                    }}>
                        close
                    </span>
                </Exit>
                <Exit onClick={() => this.handleNewList()}>
                    <span className="material-icons" style={{
                        color: '#eb3b28',
                        fontSize: '44px',
                        cursor: 'pointer',
                    }}>
                        panorama_fish_eye
                    </span>
                </Exit>
                <ListPrompt color={pal[5]}>
                    New List Name:
                </ListPrompt>
                <ListInput
                    ref={this.inputRef}
                    border={pal[2]}
                    color={pal[6]}
                    placeholder={"New List"}
                    onChange={e => this.inputChange(e)}
                >
                </ListInput>
                <Tip color={pal[1]}>
                    (press 'enter' to submit)
                </Tip>
            </NewListModal>
        );
    }
}

export default ListModal;

const NewListModal = styled.div`
    display: flex;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: #00000042;
    background: radial-gradient(circle, #00000042 0%, #00000069 85%);;
    align-items: center;
    justify-content: center;
    flex-direction: column;
`;

const ListInput = styled.input`
    text-align: center;
    background: transparent;
    border: none;
    border-bottom: 1px solid ${props => props.border};
    font-family: Spartan;
    font-size: 24px;
    font-weight: 500;
    color: ${props => props.color};
    width: 200px;
    transition: border-bottom 0.3s, width 0.4s;
    :hover {
        border-bottom: 2px solid ${props => props.color};
        width: 500px;
    }
    :focus {
        border-bottom: 2px solid ${props => props.color};
        width: 500px;
    }
    ::placeholder {
        font-size: 16px;
    }
`;

const Exit = styled.div`
    padding: 4px;
    position: absolute;
    top: 16px;
    left: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
`;

const ListPrompt = styled.p`
    font-family: Spartan;
    font-size: 38px;
    font-weight: 400;
    color: ${props => props.color};
`;

const Tip = styled.p`
    font-family: Spartan;
    font-size: 8px;
    font-weight: 500;
    color: ${props => props.color};
`;