import React, { Component } from "react";
import { connect } from "react-redux";
import styled, { keyframes } from 'styled-components';

import { myFirebase, db } from '../firebase/firebase';
import ListModal from './modals/ListModal';
import DelListModal from './modals/DelListModal';
import SettingsModal from './modals/SettingsModal';
import TodoModal from './modals/TodoModal';
import logo from './images/decagon.svg';

import { logoutUser } from "../actions/";

class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            theme: 1,
            currentList: 0,
            lName: '',
            title: '',
            newList: false,
            delList: false,
            newTodo: false,
            openSettings: false,
            loaded: false,
            alphabetical: false,
        }

        this.palette = [
            // Primary, Secondary, Border, Accent, Accent Hover, Font, Highlight, 
            // 0,       1,         2,      3,      4,            5,    6
            // Delete, Delete Hover,
            // 7,      8,

            ['#f5f5f5', '#f0f0f0', '#b9b9b9', '#5672e8', '#4a86ff', '#242429', '#6969691a',
             '#d44f35', '#f5230c',],
            ['#2c2d33', '#242429', '#7d7d85', '#1be3f2', '#00ffff', '#f0f0f0', '#ffffff1a',
             '#cc3318', '#f54114',],
        ]

        this.user = myFirebase.auth().currentUser.uid;
        console.log(this.user);
        this.userDoc = db.collection('users').doc(this.user);
        this.data = null;

        this.loadData();
    }

    initUser = () => {
        return new Promise(resolve => {
            let lsts = {}
            lsts[new Date().getTime()] = {title: 'Z', todos: {}};
            lsts[new Date().getTime() + 1] = {title: 'Homework', todos: {}};
            lsts[new Date().getTime() + 2] = {title: 'Extraneous', todos: {}};
            this.userDoc.set({
                theme: 0,
                alpha: false,
                lists: lsts,
            })
            .then(() => {
                this.userDoc.get().then((docSnap) => {
                    resolve(docSnap.data());
                })
                console.log('User Initialized');
            })
            .catch(function(error) {
                console.error('Error Initializing User: ', error);
            });
        })
    }

    loadData = async () => {
        this.userDoc.get().then(async (docSnap) => {
            if (docSnap.exists) {
                //this.data = await this.initUser();
                this.data = docSnap.data();
            } else {
                this.data = await this.initUser();
            }
            this.setState({
                theme: this.data.theme,
                alphabetical: this.data.alpha,
                title: 'All',
                loaded: true
            });
        });
    }

    toggleSettings = () => {
        this.setState({ openSettings: !this.state.openSettings });
    }

    toggleDark = () => {
        this.setState({ theme: 1 - this.state.theme }, function () {
            this.userDoc.update({
                theme: this.state.theme,
            });
        });
    }

    toggleAlpha = () => {
        if (this.state.currentList === 0) {
            this.setState({
                alphabetical: !this.state.alphabetical,
                title: 'All',
            }, function() {
                this.userDoc.update({
                    alpha: this.state.alphabetical,
                });
            });
        } else {
            let sorted = {}
            switch (this.state.alphabetical) {
                case true:
                    sorted = Object.keys(this.data.lists);
                    break
                case false:
                    var dat = this.data.lists;
                    Object.keys(dat).sort(function(a, b) {
                        return dat[a].title.toUpperCase() > dat[b].title.toUpperCase();
                    }).forEach(function(key) {
                        sorted[key] = dat[key];
                    });
                    sorted = Object.keys(sorted);
                    break
                default:
                    console.log('Non-boolean passed when boolean was expected...');
            }
            this.setState({
                alphabetical: !this.state.alphabetical,
                title: this.data.lists[this.state.lName].title,
                currentList: sorted.indexOf(this.state.lName) + 1,
            }, function() {
                this.userDoc.update({
                    alpha: this.state.alphabetical,
                });
            });
        }
    }

    handleNewList = () => {
        this.setState({ newList: !this.state.newList });
    }

    handleListSubmit = (val) => {
        let l = this.data.lists;
        if (val.length === 0) {
            val = 'New List';
        }
        let tim = new Date().getTime();
        l[tim] = {title: val, todos: {}};
        this.userDoc.update({
            lists: l,
        });
        if (this.state.alphabetical) {
            let sort = {};
            let dat = this.data.lists;
            Object.keys(dat).sort(function(a, b) {
                return dat[a].title.toUpperCase() > dat[b].title.toUpperCase();
            }).forEach(function(key) {
                sort[key] = dat[key];
            });
            sort = Object.keys(sort);

            this.setState({
                currentList: sort.indexOf(tim.toString()) + 1,
                title: val,
                lName: tim,
            });
        } else {
            this.setState({
                currentList: Object.keys(l).length,
                title: val,
                lName: tim,
            });
        }
    }

    initListDelete = () => {
        this.setState({ delList: !this.state.delList });
    }

    handleListDelete = () => {
        let l = this.data.lists;
        let sort = {};
        if (this.state.alphabetical) {
            let dat = this.data.lists;
            Object.keys(dat).sort(function(a, b) {
                return dat[a].title.toUpperCase() > dat[b].title.toUpperCase();
            }).forEach(function(key) {
                sort[key] = dat[key];
            });
            sort = Object.keys(sort);
            delete l[sort[this.state.currentList - 1]];
        } else {
            delete l[Object.keys(l)[this.state.currentList - 1]];
        }
        this.userDoc.update({
            lists: l,
        });
        if (this.state.alphabetical) {
            this.setState({
                currentList: this.state.currentList - 1,
                title: l[sort[this.state.currentList - 2]].title,
            });
        } else {
            this.setState({
                currentList: this.state.currentList - 1,
                title: l[Object.keys(l)[this.state.currentList - 2]].title,
            });
        }
    }

    handleLogout = () => {
        const { dispatch } = this.props;
        dispatch(logoutUser());
    };

    handleNewTodo = () => {
        this.setState({ newTodo: !this.state.newTodo });
    }

    createNewTodo = (ti, due, desc, pushTo = this.state.lName) => {
        let l = this.data.lists;
        l[pushTo].todos[new Date().getTime()] = {
            title: ti,
            dueDate: due,
            description: desc,
            comments: {},
            children: {},
        }
        this.userDoc.update({
            lists: l,
        });
    }

    clicked = (k, cl) => {
        let l = this.data.lists;
        delete l[cl].todos[k];
        this.userDoc.update({
            lists: l,
        });
        this.setState(this.state);
    }

    render() {
        var pal = this.palette[this.state.theme];
        let listPass = [];
        if (this.state.loaded) {
            var l = [];
            var t = [];
            var modal = [];

            l.push(
                <List
                    key = {'all'}
                    underline={pal[5]}
                    onClick={() => {
                        if (this.state.currentList !== 0) {
                            this.setState({
                                currentList: 0,
                                title: 'All',
                            })
                        }
                    }}
                    shade={['#00000000', pal[6]][+(this.state.currentList===0)]}
                >
                    All
                </List>
            );
            var sorted = {}
            if (!this.state.alphabetical) {
                sorted = Object.keys(this.data.lists);
            } else {
                var dat = this.data.lists;
                
                Object.keys(dat).sort(function(a, b) {
                    return dat[a].title.toUpperCase() > dat[b].title.toUpperCase();
                }).forEach(function(key) {
                    sorted[key] = dat[key];
                });
                sorted = Object.keys(sorted);
            }
            for (let i = 1; i < sorted.length + 1; i++) {
                let k = Object.keys(this.data.lists[sorted[i-1]].todos);
                var msg = this.data.lists[sorted[i - 1]].title;
                l.push(
                    <List key={i}
                        underline={pal[5]}
                        onClick={() => {
                            if (this.state.currentList!==i) {this.setState({
                                currentList: i,
                                title: this.data.lists[sorted[i - 1]].title,
                                lName: sorted[i - 1],
                            })}
                        }}
                        shade={['#00000000', pal[6]][+(this.state.currentList===i)]}
                    >
                        {msg}
                    </List>
                );
                if (i === this.state.currentList || this.state.currentList === 0) {
                    for (let j = 0; j < k.length; j++) {
                        t.push(
                            <Item key={k[j]}
                                underline={pal[5]}
                                onClick={() => {
                                    this.clicked(k[j], sorted[i-1])
                                }}
                            >
                                {this.data.lists[sorted[i-1]].todos[k[j]].title}
                                {this.data.lists[sorted[i-1]].todos[k[j]].dueDate &&
                                    <DueDate col={pal[2]}>
                                        {this.data.lists[sorted[i-1]].todos[k[j]].dueDate}
                                    </DueDate>
                                }
                                {(this.state.currentList === 0) &&
                                    <DueDate col={pal[2]}>
                                        {this.data.lists[sorted[i-1]].title}
                                    </DueDate>
                                }
                            </Item>
                        )
                    }
                }
            }

            if (this.state.newList) {
                modal.push(
                <ListModal
                    key={0}
                    listHandler={this.handleNewList}
                    submit={this.handleListSubmit}
                    t={this.state.theme}
                />
                );
            } else if (this.state.delList) {
                modal.push(
                <DelListModal
                    key={1}
                    listHandler={this.initListDelete}
                    submit={this.handleListDelete}
                    t={this.state.theme}
                />
                );
            } else if (this.state.newTodo) {
                modal.push(
                <TodoModal
                    key={2}
                    subHandler={this.createNewTodo}
                    t={this.state.theme}
                    close={this.handleNewTodo}
                    v={this.state.newTodo}
                    unspecified={this.state.currentList}
                    lists={listPass}
                    k={sorted}
                />
                );
            }

            sorted.forEach((key) => {
                listPass.push(this.data.lists[key].title);
            })
        }

        // ul, li, a, {svg, span text}
        if (!this.state.loaded) {
            return (
                <Loading>
                    <Logo src={logo} alt='Logo' />
                    Loading...
                </Loading>
            )
        }

        return (
            <TopLevel primary={pal[0]}>
                {modal}
                {this.state.openSettings &&
                    <SettingsModal
                        t={this.state.theme}
                        a={this.state.alphabetical}
                        themeHandler={() => {this.toggleDark()}}
                        sortHandler={() => {this.toggleAlpha()}}
                        returner={() => {this.toggleSettings()}}
                        logOff={() => {this.handleLogout()}}
                    />
                }
                <SideBar
                    secondary={pal[1]}
                    border={pal[2]}
                    font={pal[5]}
                >
                    Lists:
                    <Lists>
                        {l}
                        <List underline={'#00000000'}>
                            <NewList
                                border={pal[5]}
                                hover={pal[4]}
                                nl={this.state.newList}
                                onClick={() => this.handleNewList()}
                            >
                                + Create New List
                            </NewList>
                        </List>
                    </Lists>
                    <SignOutHolder
                        border={pal[2]}
                        secondary={pal[1]}
                    >
                        <SignOutButton
                            onClick={() => this.toggleSettings()}
                            accent={pal[3]}
                            accentHover = {pal[4]}
                        >Settings</SignOutButton>
                    </SignOutHolder>
                </SideBar>
                <MainContainer>
                    <Title color={pal[5]}>
                        {this.state.title}
                    </Title>
                    <TaskHolder>
                        <Breaker />
                        <Spacer>
                            {(t.length > 0) &&
                                <Todoul col={pal[5]}>
                                    {t}
                                </Todoul>
                            }
                            <AddTodo
                                border={pal[5]}
                                hover={pal[4]}
                                nl={this.state.newList}
                                onClick={this.handleNewTodo}
                                current={this.currentList}
                            >
                                + New Item
                            </AddTodo>
                        </Spacer>
                        <Breaker />
                        {this.state.currentList !== 0 &&
                            <DeleteList
                                onClick={() => this.initListDelete()}
                                accent={pal[7]}
                                accentHover={pal[8]}
                            >
                                Delete List
                            </DeleteList>
                        }
                    </TaskHolder>
                </MainContainer>
            </TopLevel>
        );
    }
}
function mapStateToProps(state) {   
  return {
    isLoggingOut: state.auth.isLoggingOut,
    logoutError: state.auth.logoutError
  };
}

export default connect(mapStateToProps)(Home);

